*{
    box-sizing: border-box;
}
body{
    background-image: url("");
    margin:0;
}
.navbar{
    display:flex;

    height:5rem;

}
.navbar h1{
    position:absolute;
    left:5%;
}
.navbar button{
    position:absolute;
    right: 10%;
    top: 1rem;
    height: 3rem;
    width: 10rem;
    font-size:1.5rem;
    border-radius: 10px;
    background: rgb(209, 203, 203);
}
.navbar button:hover{
    background: white;
}
.cardsection{
    border:1px solid;
    min-height:87vh;
    display:flex;
    flex-wrap:wrap;
    justify-content: center;
    align-items: center;
}
.load{
    height:80vh;
    width:100vw;
    display:flex;
    justify-content: center;
    align-items:center;
}
.card{
    width:28rem;
    text-align:center;
    margin:3rem;
    border-radius:10px;
    box-shadow:2px 2px 2px 1px;
    background-color: white;
    transition: transform ease 0.2s;
    opacity: 0.7;
}
.avatar{
    float: right;
    border-radius:50%;
    margin-top:1rem;
}
.name{
    font-size:2.5rem;
    color:blue;
    margin-top:0.5rem;
}
.email,.id{
    font-size:1.3rem;
}
.email{
    margin-top:-1rem;
}
.card:hover{
    transform: scale(1.1);
    opacity: 1;
}







@media( max-width:500px){
.navbar h1{
    position:absolute;
    left:2%;
}
.navbar button{
    position:absolute;
    right: 10px;
    top: 1rem;
    height: 3rem;
    width: 6rem;
    font-size:1rem;
    border-radius: 10px;
    background: rgb(209, 203, 203);
}
.card{
    height:26rem;
}
}

.brand-and-toggler {
  font-size: 1.65rem;
  padding: 0.8rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-brand {
  padding-left: 15%;
  font-weight: 500;
}
